
interface ExplodeConfig {
    cloneTarget?: boolean,
    scale?: number, 
    duration?: number, 
    onStart?: Function,
    onComplete?: Function,
    behind?: boolean,
    delay?: number,
    tint?: number,
    texture?: string
}

const explode = (target: Phaser.GameObjects.Sprite, config: ExplodeConfig = {}) => {
    config = { ...{ cloneTarget: true, scale: 3, duration: 150 }, ...config };

    let scene = target.scene;
    let sprite = config.cloneTarget ? scene.add.sprite(target.x, target.y, config.texture ?? target.texture) : target;
        sprite.setScale(0, 0);
        sprite.alpha = target.alpha;
        sprite.tint = config.tint ?? target.tint;
        sprite.setDisplaySize(target.displayWidth, target.displayHeight);
        sprite.setOrigin(target.originX, target.originY);
        sprite.depth = config.behind ? sprite.depth - 1 : sprite.depth + 1;
        
        return new Promise((res, rej) => {
            scene.tweens.add({
                targets: sprite,
                scaleX: config.scale,
                scaleY: config.scale,
                alpha: 0,
                duration: config.duration,
                delay: config.delay ?? 0,
                onStart: () => {
                    sprite.alpha = target.alpha;
                    config.onStart?.();
                },
                onComplete: () => {
                    sprite.alpha = 0;
                    config.onComplete?.();
                    res(null);
                    sprite.destroy();
                },
            });
        })
}


